import React from 'react';
import { TouchableOpacity, Text, StyleSheet } from 'react-native-web';
import { isCellEmpty } from '../../utils/gameLogic';

const Cell = ({ value, onPress, disabled = false, cellSize, fontSize, gameMode = 'local' }) => {
  const isEmpty = isCellEmpty(value);
  const isClickable = !disabled && isEmpty;

  const cellStyle = {
    ...styles.cell,
    width: cellSize,
    height: cellSize,
    borderWidth: cellSize * 0.02,
    opacity: disabled && isEmpty ? 0.5 : 1,
  };

  const textStyle = {
    ...styles.text,
    fontSize: fontSize,
    color: value === 'X' ? '#2196F3' : '#F44336',
  };

  return (
    <TouchableOpacity
      style={cellStyle}
      onPress={onPress}
      disabled={!isClickable}
      activeOpacity={isClickable ? 0.7 : 1}
    >
      <Text style={textStyle}>{value}</Text>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  cell: {
    borderColor: '#333',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#fff',
  },
  text: {
    fontWeight: 'bold',
  },
});

export default Cell;