import React from 'react';
import { View, StyleSheet } from 'react-native-web';
import Row from '../Row/Row';

const Board = ({ 
  board, 
  onCellPress, 
  gameStatus, 
  currentPlayer, 
  cellSize, 
  fontSize,
  disabled = false,
  gameMode = 'local'
}) => {
  const handleCellPress = (row, col) => {
    if (!disabled) {
      onCellPress(row, col);
    }
  };

  const boardStyle = {
    ...styles.board,
    width: board.length * cellSize,
    height: board.length * cellSize,
  };

  return (
    <View style={boardStyle}>
      {board.map((row, rowIndex) => (
        <Row
          key={rowIndex}
          row={row}
          rowIndex={rowIndex}
          onCellPress={handleCellPress}
          cellSize={cellSize}
          fontSize={fontSize}
          disabled={disabled}
          gameMode={gameMode}
        />
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  board: {
    borderWidth: 2,
    borderColor: '#333',
    backgroundColor: '#fff',
  },
});

export default Board;